import { TextField, List, ListItem } from '@mui/material'
import { Container, Stack } from '@mui/system'
import React from 'react'
import { useState } from 'react'
import TopBar from '../components/TopBar'

//Add table name and adress

export default function Kontakt() {
    let options = ["a", "ab", "abc", "abab", "acbabbab"]
    const [searchQuery, setSearchQuery] = useState("")
  return (
    <Container maxWidth="lg">
    <TopBar></TopBar>
    <Stack height="100%" width="100%">
        <TextField onChange={(e)=>{setSearchQuery(e.target.value)}} label="Wen suchen Sie?" fullWidth></TextField>
        <Stack height="70vh" overflow={"scroll"}>
            <List>
                {options.filter(option => option.includes(searchQuery)).map(element => <ListItem>{element}</ListItem>)}
            </List>
        </Stack>
    </Stack>
    </Container>
  )
}
